import './Humidity.css'

export const Humidity = ({ data }) => {
  if (!data) return null

  return (
    <div className="humidity-card card">
      <h4></h4>

      <div className="card-content humidity-content">
        <div className="humidity-value">{data.humidity}%</div>
        <p className="humidity-desc">
          {data.humidity < 40
            ? ' '
            : data.humidity < 60
              ? ''
              : data.humidity < 80
                ? ''
                : ' '}
        </p>
      </div>
    </div>
  )
}
