import './UVIndex.css'

export const UVIndex = ({ data }) => {
  if (!data) return null

  return (
    <div className="uv-card card">
      <h4>UV </h4>

      <div className="card-content uv-content">
        <div className="uv-value">{data.uv}</div>
        <p className="uv-desc">
          {data.uv < 3 ? '' : data.uv < 6 ? '' : data.uv < 8 ? '' : ''}
        </p>
      </div>
    </div>
  )
}
