import './Visibility.css'

export const Visibility = ({ data }) => {
  if (!data) return null

  const km = Number(data.vis_km)

  const getQuality = () => {
    if (km >= 10) return ''
    if (km >= 6) return ''
    if (km >= 2) return ''
    return ' '
  }

  const getDescription = () => {
    if (km >= 10) return ' .    .'
    if (km >= 6) return '  .'
    if (km >= 2) return '     .'
    return ' ,  .'
  }

  return (
    <div className="visibility card wide">
      <h4></h4>

      <div className="vis-item">
        <span className="vis-label"></span>
        <span className="vis-number">{km}</span>
      </div>

      <div className="vis-quality">{getQuality()}</div>
      <div className="vis-description">{getDescription()}</div>
    </div>
  )
}
