import './DewPoint.css'

export const DewPoint = ({ data }) => {
  if (!data) return null

  return (
    <div className="dew-card card">
      <h4> </h4>

      <div className="card-content dew-content">
        <div className="dew-value">{data.dewpoint}</div>
        <p className="dew-desc">
          {data.dewpoint < 5
            ? ''
            : data.dewpoint < 12
              ? ''
              : data.dewpoint < 18
                ? ' '
                : ' '}
        </p>
      </div>
    </div>
  )
}
